*,
*::before,
*::after {
  box-sizing: border-box;
}
:root {
    --input-border: #8b8a8b;
    --input-focus-h: 245;
    --input-focus-s: 100%;
    --input-focus-l: 42%;
  }
  
  

.wrapper {
    position: relative;
    height: 100vh;
    width: 100vw;
    /* filter: blur(3px);
    background-size: auto 100%;
    background-position: center center;
    background-image: url('https://i.ibb.co/bvQ3sX5/home.jpg');
    overflow: hidden; */
}

.backgroundImage {
    /* The image used */
    background-image: url("https://i.ibb.co/bvQ3sX5/home.jpg");
    /* Add the blur effect */
    filter: blur(3px);
    -webkit-filter: blur(3px);
    /* Full height */
    height: 100%;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.content {
    height: 400px;
    width: 700px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    /* padding: 20px; */
    
    display: flex;
    justify-content: space-between;

    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);

    overflow: hidden;
}

.leftSide {
  
  flex: 2;
  background-color: rgba(155,222,186, 0.5);

  font-size: 4vh;;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.leftSide div {
  padding: 0 15px 0 15px;
  margin-bottom: 20px;
}
.leftSide img {
  width: 80px;
  height: 80px;
}

.rightSide {
  flex: 3;
  background-color: rgba(255,122,115, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.rightSideInput {
  margin-bottom: 40px;
}

.form {
    height: inherit;
    width: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 10px;
    background-color: rgba(255,122,115, 0.5);
    /* filter: blur(10px); */
    /* border-filter: blur(20px);
    backdrop-filter: blur(20px); */
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}


